#footer {
	clear: both;
	margin: 50px 0 0 0;
	color: #fff;
	background-color: #041022;
	padding: 30px 0;
	font-weight: 300;
	@include breakpoint($sm){
		padding: 15px 0;
		font-size: rem(13);
	}
	.container {
		position: relative;
		max-width: $pc-wide;
		text-align: center;
		@include breakpoint($lg){
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-left: 10px;
			margin-right: 10px;
			padding: 0;
			text-align: left;
		}
		@include breakpoint(min-width 1720px) {
			margin-left: auto;
			margin-right: auto;
		}
		@include breakpoint($sm){
			padding: 20px 0 0 0;
		}		
	}	
	.footer-nav {
		ul {
			display: flex;
			@include breakpoint($smd){
				flex-wrap: wrap;
				max-width: 410px;
				margin: 0 auto;
			}
			@include breakpoint($sm){
				max-width: 315px;
			}
			li {
				margin-right: 14px;
				@include breakpoint($smd){
					margin-top: 2px;
					margin-bottom: 2px;
				}
				&:last-child {
					margin-right: 0;
				}
				a {
					position: relative;
					color: #fff;
					padding-left: 15px;
					display: block;
					white-space:nowrap;
					&::before {
						content: "";
						display: inline-block;
						vertical-align: middle;
						width: 4px;
						height: 4px;
						background-color: #fff;
						border-radius: 50%;
						//margin-right: 10px;
						position: absolute;
						top: 50%;
						margin-top: -2px;
						left: 0;
					}
				}
				&.point {
					a {
						color: #6db5f9;
						font-weight: 500;
						&::before {
							background-color: #6db5f9;
						}
					}
				}
			}
		}
	}
	.footer-bottom {
		address {
			margin-top: 16px;
			font-style: normal;
		}
		.copyright {
			font-size: rem(14);
			@include breakpoint($sm){
				margin-top: 5px;
			}
		}
	}	
	.f_r {
		@include breakpoint($smd){
			margin-top: 20px;
		}
		.f_sns {
			margin-bottom: 10px;
			button {
				width: 36px;
				height: 36px;
				background-color: rgba(255, 255, 255, 0.6);
				border-radius: 50%;
				margin: 0 2px;
				&::before {
					content: "";
					display: inline-block;
					width: 36px;
					height: 36px;
					background-position: 50%;
					background-repeat: no-repeat;
				}
			}
		}
	}	
}